.chat-input-text {
	/* float: left; */
	/* max-height: 40px; */
	height: 40px;
	border: none;
	padding-top: 12px;
	padding-left: 15px;
	border-radius: 40px;
	width: 100%;
	background-color: white;
	box-shadow: inset 0 0 0 2px #006fbb;
	font-size: 13px;
	transition: 0.15s all ease-in-out;
	box-sizing: border-box;
	outline: none;
	resize: none;
	overflow-y: hidden;
}
.post-text {
	font-size: 20px;
	line-height: 24px;
	font-weight: 400;
	padding: 10px;
}

.Polaris-TextField__Backdrop {
	border: 0px none !important;
}

.ig-post-btn:hover {
	cursor: pointer;
}

textarea {
	font-size: 14px !important;
	color: #444;
	line-height: 1.4 !important;
	padding: 15px 0px 0px 15px !important;
	min-height: 50px !important;
}

textarea:hover {
	border: 0px none !important;
}

.fb-chatview-btn:hover {
	cursor: pointer;
}